import React, { Component } from 'react'

// Redux
import { connect } from 'react-redux'
import { createPost, deletePost, editPost, updatePost } from '../actions/postActions'

// React Components
import PostItem from './PostItem'
import PostForm from './PostForm'

import {  ButtonDropdown,
          DropdownToggle,
          DropdownItem,
          DropdownMenu } from 'reactstrap'

class PostView extends Component {
  constructor(){
    super()
    this.state = {
      posts: [],
      newPost: {},
      dropdownOpen: false
    }

    this.onSubmit = this.onSubmit.bind(this)
    this.toggleDropdown = this.toggleDropdown.bind(this)
  }

  onDelete(id){
    console.log('DELETE_POST id: ' + id)
    // let posts = this.props.posts
    // let i = posts.findIndex(x => x.id === id)
    // posts.splice(i, 1)
    // this.setState({posts: posts})
    this.props.deletePost(id)
  }

  onEdit(id){
    this.props.editPost(id)
    console.log('EDIT_POST id:' + id)
  }

  onSave(post){
    this.props.updatePost(post)
  }

  onSubmit(e, newPost){
    e.preventDefault()
    if(newPost.title !== ''){
        this.props.createPost(newPost)
    }
  }

  toggleDropdown() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    })
  }

  componentWillReceiveProps(nextProps){
    if(nextProps.newPost){
      this.props.posts.push(nextProps.newPost)
    }
  }

  render() {

    // const postItems = this.props.posts.map(post => (
    //     <PostItem key={post.id} post={post} />
    // ))

    return (
      <div>
        <PostForm onSubmit={this.onSubmit} />
        <hr/>
        <h2>Latest Posts</h2>
        <ButtonDropdown isOpen={this.state.dropdownOpen} toggle={this.toggleDropdown}>
          <DropdownToggle color='primary' caret>
            Sort by
          </DropdownToggle>
          <DropdownMenu>
            <DropdownItem header>Sort by...</DropdownItem>
            <DropdownItem>Title</DropdownItem>
            <DropdownItem>Body</DropdownItem>
          </DropdownMenu>
        </ButtonDropdown>
        <br/>
        <br/>
        {this.props.posts.map((post) => (
          <div>
            <PostItem
              key={post.id} post={post}
              onDelete={this.onDelete.bind(this)}
              onEdit={this.onEdit.bind(this)}
              onSave={this.onSave.bind(this)}
            />
            <br/>
          </div>
        ))}
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    posts: state.posts.items,
    newPost: state.posts.item
  }
}

export default connect(mapStateToProps, { createPost, deletePost, editPost, updatePost })(PostView)


